.header__top-inner, .header__nav, .goods__container {
  max-width: 1180px;
  margin: auto; }

.header__input, .header__contact {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px; }

.header__phone, .header__link {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 29px; }

.header__top-btn, .goods__btn {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 14px; }

.goods__title {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 36px;
  line-height: 58px; }

.header {
  box-shadow: 0px 0px 10px 0px #A4A4A408;
  box-shadow: -3px 0px 40px 0px #0000001A; }
  .header__top {
    border-bottom: 1px solid #EBEBF1; }
  .header__top-inner {
    padding: 15px 5px 18px; }
  .header__top-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    @media screen and (max-width: 1060px) {
      .header__top-wrap {
        flex-wrap: wrap;
        gap: 15px;
        flex-direction: column;
        align-items: center; } }
  .header__top-main {
    max-width: 1040px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    @media screen and (max-width: 965px) {
      .header__top-main {
        flex-wrap: wrap;
        gap: 15px;
        flex-direction: column;
        align-items: center; } }
  .header__logo {
    width: 135px;
    height: 30px; }
  .header__search {
    max-width: 660px;
    width: 100%;
    height: 40px;
    padding: 9px 20px 9px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: transparent;
    border-radius: 5px;
    background: #FAFAFA; }
    .header__search:hover {
      outline: 1px solid #121920;
      /*Firefox 19+*/
      /*Firefox 18-*/ }
      .header__search:hover ::-webkit-input-placeholder {
        color: #121920; }
      .header__search:hover ::-moz-placeholder {
        color: #121920; }
      .header__search:hover :-moz-placeholder {
        color: #121920; }
      .header__search:hover :-ms-input-placeholder {
        color: #121920; }
    .header__search:hover #search-icon {
      fill: #121920; }
    .header__search:hover #header-input {
      color: #121920; }
  .header__input {
    padding-right: 10px;
    outline: none;
    border: none;
    max-width: 600px;
    width: 100%;
    background: #FAFAFA;
    color: #BBC4D6; }
    .header__input:hover {
      color: #121920; }
  .header__submit {
    width: 16px;
    height: 16px;
    border: none;
    outline: none;
    background: url(./../assets/images/search.svg);
    cursor: pointer; }
  .header__contact {
    width: 143px;
    height: 51px;
    color: #BBC4D6;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    margin-right: 10px; }
    .header__contact:hover #header-call {
      display: none; }
    .header__contact:hover #header-work {
      display: block; }
    .header__contact:hover #header-phone {
      color: #34AADD; }
  .header__phone {
    color: #121920;
    text-decoration: none; }
  .header__work {
    display: none; }
  .header__top-btn {
    width: 100px;
    height: 40px;
    background: #34AADD;
    border: transparent;
    border-radius: 5px;
    color: #FFFFFF;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center; }
    .header__top-btn:hover {
      background: #6680AE; }
    .header__top-btn:active {
      background: #BBC4D6; }
    .header__top-btn:disabled {
      background: #EBEBF1; }
  .header__nav {
    padding: 0 5px; }
  .header__list {
    max-width: 786px;
    padding-top: 16px;
    padding-bottom: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 60px;
    list-style-type: none; }
    @media screen and (max-width: 795px) {
      .header__list {
        flex-wrap: wrap;
        flex-direction: column;
        align-items: center;
        gap: 15px; } }
  .header__link {
    text-decoration: none;
    color: #768298; }
    .header__link:hover {
      color: #34AADD;
      transform: translateY(-3px); }
    .header__link:active {
      opacity: 0.5; }

input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration {
  display: none; }

.goods__container {
  padding: 90px 5px; }

.goods__title {
  color: #121920;
  text-transform: uppercase;
  margin-bottom: 50px; }

.goods__carts {
  display: grid;
  grid-template-columns: repeat(4, 270px);
  column-gap: 30px; }

.goods__btn {
  width: 205px;
  height: 50px;
  padding-left: 30px;
  border: 1px solid #34AADD;
  border-radius: 5px;
  background: #FFFFFF;
  cursor: pointer;
  margin-top: 30px;
  color: #34AADD;
  display: flex;
  align-items: center;
  gap: 10px; }
  .goods__btn:hover {
    border-color: #6680AE;
    color: #6680AE; }
    .goods__btn:hover #arrow-right {
      fill: #6680AE; }
  .goods__btn:active {
    border-color: #BBC4D6;
    color: #BBC4D6; }
    .goods__btn:active #arrow-right {
      fill: #BBC4D6; }
  .goods__btn:disabled {
    border-color: #EBEBF1;
    color: #EBEBF1; }
    .goods__btn:disabled #arrow-right {
      fill: #EBEBF1; }

.footer__top {
  background-color: #f6a839;
  margin-top: 5px; }

.footer__top-wrapper {
  max-width: 1720px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 80px 0 95px; }
